.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
ul {
  list-style: none;
  text-align: left;
  margin: unset;
  padding: unset;
  content:"\2714\0020"
}

ul li:before {
  content:"\2714\0020";
  margin-right: 1em;
}
li {
  margin-top: .5em;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mb-left-lg {
  margin-left: 1em;
}
@media (max-width: 1199.98px) { 
  .mb-left-lg {
    margin-left: unset;
  }
}
